.main {
    position: fixed;
    left: 16.8rem;

        .inicio {
            position: fixed;
            left: 16.8rem;
            top: 0;
            right: 0;
            height: 100vh;
            background-color: black;
            opacity: 1;
            z-index: 0;
            overflow-y: auto;
        }

        .prod__titulo__principal {
            font-size: 3rem;
            color: #fff;
            font-family: $fonte-titulo;
            color: $cor-titulo;
            text-align: center;
        }

        .prod__box {
            display: flex;
            flex-wrap: wrap;
            gap: 1rem;
            justify-content: center;
            align-items: center;
            height: 100vh;
        }

        .prod__nome {
            color: #fff;
            font-family: $fonte-titulo;
            font-size: 1.5rem;
        }

        .prod__desc {
            color: #fff;
            font-family: $fonte-corpo;
            font-size: 1.3rem;
            margin-bottom: 3rem;
            width: 320px;
            // background-color: teal
        }

        .prod__link {
            @include btn-config;
            box-shadow: 0px 2px 10px #fff;

                &:hover {
                    background-color: #fff;
                    transition: all .6s;
                }
        }

        .img__prod {
            width: 600px;
        }




}

